<template>
<div v-loading.fullscreen="loading">

  <div class="pbox">
      <div>
        <p class="pbox_title"> {{ $t("fov.logan_name") }} </p>
      </div>

      <img class="pagopa_icon" src="../assets/trx.png" />
      <div class="pagopa-info">
          <p class="pagopa-label">{{ $t("fov.level") }}: V{{ level }}</p>

          <p v-if="levelCount > 0" class="rate-label">{{ $t("fov.level_count") }}: {{ levelCount }} TRX</p>
          <p v-else class="rate-label"> {{ $t("fov.start_des") }} </p>
      </div>

      <div class="lan">
        <el-link @click="langToggle"  >{{ $t("fov.lang") }}</el-link>
      </div>
  </div>

  <div class="content">
    <div class="open-info">
        
    </div>

    <div>
      <div class="depositTip">
        <p><span>{{ $t("fov.input_hint") }}(100-100000)：</span></p>
        <p><b>{{ earnDay }}</b>{{ $t("fov.day_earn") }}<b>{{ earnP }}</b>%</p>
      </div>
      <input  class="depositInput" v-model="invest" type="number"  min="100" max="100000"/>
    </div>

    <div class="jbox">
        <el-button class="join" size="big" round @click="join">
            {{ $t("fov.join_now") }}
        </el-button>
    </div>

    <div class="title-space">
        <b>{{ $t("fov.earned_title") }}</b>

        <div class="link_right">
          <el-link  size="small" type="info" href="#/order">{{ $t("fov.mine_log") }} </el-link>
        </div>
    </div>

    <div class="card-bkg ">
      <div class="mine-box" >
        <div class="award-des">
          <span>{{ $t("fov.mine_done") }}:</span>
          <b> {{ canWdEth }}  </b> trx
        </div>
        <el-button size="small" type="warning" @click="withdraw">{{ $t("fov.wd_btn") }}</el-button>
      </div>

      <div class="mine-box">
        <div class="award-des">
          <span>{{ $t("fov.earned") }}:</span>
          <b > {{ withdrawEarned }} </b>trx
        </div>
      </div>
    </div>

    <div class="title">
        <b>{{ $t("fov.partners") }}</b>
    </div>

    <div class="card-bkg" >
      <div class="mine-box">
        <div class="award-des">
            <span> {{ $t("fov.partner_count") }}: </span>
            <b> {{ parnerNum }}</b>
        </div>
        <el-link type="info" href="#/node">{{ $t("fov.details") }} </el-link>
      </div>
      
      <div class="mine-box">
        <div class="award-des">
          <span>{{ $t("fov.invite") }}:</span>
          <b class="copylinktext" >&nbsp; {{ uLink }} </b>
        </div>
      </div>
    </div>

  </div>
</div>
</template>

<script>
/* eslint-disable */
import formatNum from "./../numcomp";
import {
    Message,
    MessageBox
} from 'element-ui';

export default {
    name: 'Fov',
    data() {
        return {
            fov: null,
            invest: 5000,
            earnDay: 5,
            earnP: 5,
            level: null,
            levelCount: null,
            withdrawEarned: null,
            parnerNum: 0,
            canWdEth: 0,
            uLink: null,
            uLinkShort: null,
            loading: false,
        }
    },

    watch: {
      invest(curVal, oldVal) {
        console.log("curVal:" + curVal)
        console.log("invest:" + this.invest)

        if (curVal <= 10000) {
            this.earnP = 5;
            this.earnDay = 5;
        } else if (curVal <= 50000) {
            this.earnP = 12;
            this.earnDay = 15;
        } else {
            this.earnP = 22;
            this.earnDay = 25;
        }
      }
    },

    created() {
        let addr = this.$route.params.p;

        console.log("p:" + addr + ", len:"  + addr.length)

        if (addr && addr.length === 34) {
            this.parentAddr = addr;
        } else {
            console.log("no parent");
        }

        this.init();
    },

    methods: {
      async init() {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          this.account = window.tronWeb.defaultAddress.base58;
          this.tronWeb = window.tronWeb;
          console.log("inited account:" + this.account);

          this.uLink = process.env.VUE_APP_HOST + 'p/' + this.account
          this.uLink.substr(0, 20)

          this.uLinkShort = this.uLink.substr(0, 20) + "..." + this.uLink.substr(this.uLink.length-4, this.uLink.length)

          this.initContract();
        } else {
          setTimeout(() => {
            this.init()
          }, 1000);
        }
      },

        fn(num) {
            return formatNum(num, 2)
        },

        fnd(num, d) {
            return formatNum(num, d)
        },



        async initContract() {
            this.fov = await await this.tronWeb.contract().at(window.FOV_ADDR);
            console.log("contract address:" + this.fov.address);
            this.loading = false;

            this.info()
            this.getParner()
        },

        langToggle() {
          if(this.$i18n.locale == 'zh_CN'){
            this.$i18n.locale = 'en_US';
            localStorage.setItem('langs','en_US');
          }else {
            this.$i18n.locale = 'zh_CN';
            localStorage.setItem('langs','zh_CN');
          }
        },

        info() {
            this.fov.investInfo(this.account).call().then(
                r => {
                    this.level = r[0].toNumber();
                    this.levelCount = this.tronWeb.fromSun(r[1])
                }
            )

            this.fov.withdrawEarned(this.account).call().then(
                r => {
                    this.withdrawEarned = this.fn(this.tronWeb.fromSun(r), 4)
                }
            )

            this.fov.canWd().call().then(r => {
                this.canWdEth = this.fnd(this.tronWeb.fromSun(r), 4);
            })
        },

        getParner() {
            this.fov.childNum(this.account).call().then(r => {
                this.parnerNum = r.toNumber();
                console.log(this.parnerNum)
            })

        },

        copyUlink() {
            if (!navigator.clipboard) {
                var textArea = document.createElement("textarea");
                textArea.value = this.uLink;
                textArea.style.position = "fixed"; //avoid scrolling to bottom
                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();

                try {
                    var successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    console.log('Fallback: Copying text command was ' + msg);

                    Message.info({
                        "message":  i18n.t("fov.msg_link_copyed")
                    })

                } catch (err) {
                }

                document.body.removeChild(textArea);
            } else {
                navigator.clipboard.writeText(this.uLink).then(function () {
                    Message.info({
                        "message": i18n.t("fov.msg_link_copyed")
                    })
                }, function (err) {
                    Message.info({
                        "message": i18n.t("fov.msg_link_copyed_fail")
                    })
                });
            }

        },

        withdraw() {
            this.fov.withdraw().send().then(
                r => {
                    this.info()
                }
            )
        },

        join() {
            if (this.invest > 100000 || this.invest < 100) {
                MessageBox.alert(i18n.t("fov.invalid_value"), i18n.t("fov.tip"), {
                    confirmButtonText: i18n.t("fov.btn_ok")
                })
                return;
            } 
            else  if (!this.parentAddr) {
                MessageBox.alert(i18n.t("fov.invalid_p"), i18n.t("fov.tip"), {
                    confirmButtonText: i18n.t("fov.btn_ok")
                })
                return;
            } else {
              console.log("invest:" + this.invest )
                this.fov.join(this.parentAddr).send({
                    callValue: this.tronWeb.toSun(this.invest)
                }).then(r => {
                    this.info()
                })
            }
        },

    }
}
</script>

