<template>
<div v-loading.fullscreen="loading">
    <div class="header">
        <span class="el-icon-back back" @click="goBack"> </span>
        <div class="header-title"> {{ $t("fov.partners") }} </div>
        <span class="back" @click="goBack"> </span>
    </div>

    <div class="table-content">

        <el-table :data="nodes" align="center" stripe style="width: 100%">
            <el-table-column prop="addr" :label=addrLabel align="left" width="180">
            </el-table-column>
            <el-table-column prop="level" :label=levelLabel align="center" width="50">
            </el-table-column>
            <el-table-column prop="joined" :label=joinedLabel align="center" width="90">
            </el-table-column>
        </el-table>

    </div>

</div>
</template>

<script>
/* eslint-disable */
import formatNum from "./../numcomp";

export default {
    name: 'Node',
    data() {
        return {
            addrLabel: i18n.t("node.addr"),
            levelLabel: i18n.t("node.level"),
            joinedLabel: i18n.t("node.joined"),

            lastAwardEth: "~",
            round: 0,
            title: null,
            loading_more: false,
            nodes: [],
            parnerNum: 0,
            loading: false
        }
    },

    created() {
      this.init()
    },

    methods: {

      goBack() {
          this.$router.back(-1)
      },

      async init() {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          this.account = window.tronWeb.defaultAddress.base58;
          this.tronWeb = window.tronWeb;
          console.log("inited account:" + this.account);

          this.uLink = process.env.VUE_APP_HOST + 'p/' + this.account
          this.uLink.substr(0, 20)

          this.uLinkShort = this.uLink.substr(0, 20) + "..." + this.uLink.substr(this.uLink.length-4, this.uLink.length)

          this.initContract();
        } else {
          setTimeout(() => {
            this.init()
          }, 1000);
        }
      },

        async initContract() {
            this.fov = await this.tronWeb.contract().at(window.FOV_ADDR);
            this.loading = false
            this.loadChilds(this.account)
        },

        fn(num) {
            return formatNum(num, 4)
        },

        async loadChilds(node) {
            let arr = await this.fov.myChilds().call();
            
            for (let i = 0; i < arr.length; i++) {
                await this.loadNodeInfo(arr[i]);
            }

        },

        async loadNodeInfo(node) {
          // console.log("fromHex node:" + )

            var info = await this.fov.investInfo(node).call();

            var childNum = await this.fov.childNum(node).call();

            // var joined = await this.fov.joined(node, {
            //     from: this.account
            // })

            var nodeInfo = {
                addr: this.tronWeb.address.fromHex(node).substring(0, 16) + "..",
                level: info[0].toNumber(),
                joined: this.tronWeb.fromSun(info[2]) + "TRX",
                num: childNum.toNumber()
            }

            this.nodes.push(nodeInfo)
            this.$forceUpdate();
        },

    },

}
</script>

<style scoped>
.loading_more {
  padding: 10px;
}

.table-content {
  padding: 5px;
}
</style>
