<template>
<div v-loading.fullscreen="loading">

    <div class="header">
        <span class="el-icon-back back" @click="goBack"> </span>
        <div class="header-title"> {{ $t("order.title") }} </div>
        <span class="back" @click="goBack"> </span>
    </div>

<div  class="content content-on-tab">


    <div class="title">
        <img src="../assets/wallet.svg" />
        <span>{{ $t("order.account") }}</span>
    </div>
    <div class="balance-content">
        <div>
            <div class="icon-content">
                <span class="gold-font">{{ $t("order.level") }}</span>
            </div>
            <p class="gold-font">V{{ level }} </p>
        </div>
        <div>
            <div class="icon-content">
                <span class="gold-font">{{ $t("order.balance") }}</span>
            </div>
            <p class="gold-font">{{ balance }} </p>
        </div>
    </div>

    <div class="card"  v-bind:key="index" v-for="(order, index) in orders">
        <div class="box_top"><span>{{ $t("order.date") }} {{order.date }}</span>  <el-tag :type=order.tagtype > {{order.wd }} </el-tag> </div>
        <div class="box_top"><span>{{ $t("order.period") }}{{order.period }}{{ $t("order.day") }}</span>  </div>
        <div class="card-divider"></div>
        <div class="box_content"><span>{{ $t("order.invest") }}<b>{{ order.value }} trx</b> </span><span>{{ $t("order.lv") }}<b>V{{order.level }}</b> </span></div>
        <div class="box_content"><span>{{ $t("order.earn_rate") }}<b>{{ order.percent }} %</b> </span><span>{{ $t("order.earn_value") }}<b>{{order.earn }} trx</b> </span></div>
        
    </div>

    <p class="tips-font ">{{ $t("order.directions") }}<br />
        {{ $t("order.des_1") }}<br />
        {{ $t("order.des_2") }}<br />
    </p>

  </div>
</div>
</template>

<script>
/* eslint-disable */
import formatNum from "./../numcomp";
import {
    MessageBox
} from 'element-ui';

export default {
    name: 'Order',
    data() {
        return {
            level: null,
            balance: null,
            levelCount: null,
            loading: true,
            orders: [],
            orderNums: null,
        }
    },

    created() {
      this.init()
    },
    methods: {

        fn(num) {
            return formatNum(num, 2)
        },

        goBack() {
            this.$router.back(-1)
        },

      async init() {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          this.account = window.tronWeb.defaultAddress.base58;
          this.tronWeb = window.tronWeb;
          console.log("inited account:" + this.account);

          this.uLink = process.env.VUE_APP_HOST + 'p/' + this.account
          this.uLink.substr(0, 20)

          this.uLinkShort = this.uLink.substr(0, 20) + "..." + this.uLink.substr(this.uLink.length-4, this.uLink.length)

          this.initContract();
        } else {
          setTimeout(() => {
            this.init()
          }, 1000);
        }
      },

        async initContract() {
            this.fov = await this.tronWeb.contract().at(window.FOV_ADDR);

            this.loading = false;

            this.info()
            this.myOrders()

        },

        info() {
            this.fov.investInfo(this.account).call().then(
                r => {
                    this.level = r[0].toNumber();
                    this.levelCount = this.tronWeb.fromSun(r[1])
                }
            )

            this.tronWeb.trx.getBalance(this.account).then(r => {
                this.balance = this.fn(this.tronWeb.fromSun(r), 4)
            })
        },

        myOrders() {
            this.orders = []
            this.fov.orderNum().call().then(r => {
                this.orderNums = r.toNumber()
                if (this.orderNums > 0) {
                    this.getOrder(this.orderNums - 1)
                }
                this.$forceUpdate();
            })
        },

        getOrder(index) {
            this.fov.order(index).call().then(r => {
                var joinTs = r[1] * 1000;
                var joinTime = new Date(joinTs)
                var dateStr = joinTime.toLocaleDateString().replace(/\//g, "-") + " " + joinTime.toTimeString().substr(0, 8);
                var level = 0;
                var statusStr = i18n.t("order.status_doing")
                var type = "success"
                var percent = 5
                var earn = 0
                var period
                let dayMilliSecond = 1000 * 60 * 24 * 60;  // 1 -> 24 * 60

                var trxValue = parseFloat(this.tronWeb.fromSun(r[0]))

                var currTime = new Date().getTime();

                if (trxValue < 10001) {
                    level = 1;
                    percent = 5
                    period = 5;
                    earn = trxValue + (trxValue * percent / 100)
                    var joinEnd = joinTs + period * dayMilliSecond;
                    if (currTime > joinEnd) {
                        statusStr = i18n.t("order.status_done")
                        type = "success"
                    }

                } else if (trxValue >= 10001 && trxValue < 50001) {
                    level = 2;
                    percent = 12;
                    earn = trxValue + (trxValue * percent / 100);
                    period = 15;
                    var joinEnd = joinTs + period * dayMilliSecond;
                    if (currTime > joinEnd) {
                        statusStr = i18n.t("order.status_done")
                        type = "success"
                    }
                } else {
                    level = 3;
                    percent = 22;
                    earn = trxValue + (trxValue * percent / 100)
                    period = 25;
                    var joinEnd = joinTs + period * dayMilliSecond;
                    if (currTime > joinEnd) {
                        statusStr = i18n.t("order.status_done")
                        type = "success"
                    }
                }

                if (r[2]) {
                    statusStr = i18n.t("order.status_wd")
                    type = "info"
                }
                let order = {
                    value: this.tronWeb.fromSun(r[0]),
                    date: dateStr,
                    wd: statusStr,
                    level: level,
                    percent: percent,
                    period: period,
                    earn: earn,
                    tagtype: type
                }

                this.orders.push(order);

                if (index > 0) {
                    this.getOrder(index - 1)
                }
                this.$forceUpdate();
            });
        }

    }
}
</script>

