<template>
<div v-if="isown" class="content content-on-tab">

    <h2>管理员后台</h2>

    <div>合约当前总资金: <b> {{ totalBalance }} TRX </b></div>

    <div style="text-align: left;">当前管理员： {{ owner }}</div>
    <div class="row-center">
        <el-input placeholder="请输新管理员地址" v-model="newOwner"></el-input>
        <el-button @click="setMgr"> 转移管理员 </el-button>
    </div>

    <div class="row-center">
        <el-input placeholder="提取数量" v-model="wdValue"></el-input>
        <el-button @click="wdFund"> 割 </el-button>
    </div>

    <div>
        起始推广链接: <el-link type="primary" :href="uLink"> {{ uLink }} </el-link>
    </div>

<div style="text-align: left;">
      <p class="tips-font ">温馨提示：<br />
        1. 转移管理员前，应该先把管理员收益提出。 <br />
        2. 当合约资金不足时，会导致用户提取资金失败。 <br />
        <br />
    </p>
</div>


</div>
</template>

<script>
/* eslint-disable */
// import formatNum from "./../numcomp";
import MyVid from "./../my_vid.js";

export default {
    name: 'Admin',
    data() {
      return {
        owner: null,
        account: null,
        uLink: null,
        fund: 0,
        isown: null,
        totalBalance: null,
        wdValue: null,
        newOwner: null,
      }
    },
    created() {
      this.init();
    },
    methods: {

      async init() {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          this.account = window.tronWeb.defaultAddress.base58;
          this.tronWeb = window.tronWeb;
          console.log("inited account:" + this.account);



          this.initContract();
        } else {
          setTimeout(() => {
            this.init()
          }, 1000);
        }
      },

        async initContract() {
          this.fov = await this.tronWeb.contract().at(window.FOV_ADDR);
            this.info()
        },

        info() {
            this.uLink = process.env.VUE_APP_HOST + 'p/' + window.FOV_ADDR

            this.tronWeb.trx.getBalance(this.fov.address).then(r => {
              this.totalBalance = this.tronWeb.fromSun(r)
            })

            this.fov.owner().call().then(r => {
                this.owner = this.tronWeb.address.fromHex(r)
                console.log(this.owner)
                console.log(this.account)
                if (this.owner.toLowerCase() == this.account.toLowerCase()) {
                    this.isown = true
                }
            })
        },

        setMgr() {
            this.fov.transferOwnership(this.newOwner).send().then(r => {
                this.info()
            })
        },

        wdFund() {
            this.fov.wdFee(
                this.tronWeb.toSun(this.wdValue)).send().then(
                r => {
                    this.info()
                }
            )
        }

    }
}
</script>

